.main-wrapper{
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.mainPhoto{
  width: 50%;
  border-radius: 10px;
}
.mainText{
  font-size: calc(10px + 2vmin);
}

@media (max-width: 1150px){
  .mainPhoto{
    width: 80%;
  }
}